import React from 'react'
import { Button } from 'antd'
import { inject, observer } from 'mobx-react/index'


@inject('mobxStore') @observer
class MobxTest extends React.Component{
  constructor(props){
    super(props)
    this.state={
      name:''
    }
    this.mobxMethods = this.mobxMethods.bind(this)
  }
  mobxMethods=()=>{
    this.props.mobxStore.test()
  }
  render(){
    return(
      <div>
        {this.props.mobxStore.textVal}
        <Button onClick={this.mobxMethods}>mobxMethods</Button>
      </div>
    )
  }
}

export default MobxTest



